import Swiper from "swiper";
import 'swiper/swiper-bundle.css';
import { addItemsToCart } from '../../services/cart';
import { onDocumentReady } from '../../utils/dom';
import { formatPrice, isFinalSale, isUnreturnable, retrieveSku } from '../../utils/product';

const PRODUCT_CARD_SCALE = 2.25;
const SLIDE_SPACE_BETWEEN = 5;
const LOOP_FALLBACK_BEFORE_SLIDES_COUNT = 2;

const tabletMedia = window.matchMedia('screen and (max-width: 1100px)');

class QuickShop {
  constructor(id) {
    this.id = id
    this.state = {
      swiper: null,
      elements: {
        optionEls: {},
      },
      prevTranslate: 0,
      currentViewProduct: null,
      variantSelection: {},
      productIds: [],
      allOptions: {},
      transitionStarted: false,
      contentCalculated: false,
      scrollInterval: {
        slidePrev: false,
        slideNext: false,
        isMoving: false,
      },
    };

    this.init()
  }

  init()  {
    (
      this.cacheElements(),
      this.state.elements.quickShopContainer && (
        this.initSwiper(),
        this.attachListeners(),
        this.matchProductOptions()
      )
    )
  }

  cacheElements () {
    const quickShopContainer = document.querySelector(`#quick-shop-${ this.id }.quick-shop`);
    if (!quickShopContainer) return;
    const productContent = quickShopContainer.querySelector('.quick-shop__product-content');
    this.state.elements = {
      ...this.state.elements,
      quickShopContainer,
      quickShopSwiperContainer: quickShopContainer.querySelector('.quick-shop__body'),
      productContent,
      optionsList: productContent.querySelector('.quick-shop__options-list'),
      priceContainer: productContent.querySelector('.quick-shop__price'),
      titleContainer: productContent.querySelector('.quick-shop__product-title'),
      colorLabelContainer: productContent.querySelector('.quick-shop__color-label span'),
      addToCartBtn: productContent.querySelector('[data-add-to-cart-action]'),
      viewProductBtn: productContent.querySelector('.quick-shop__btn-view'),
      comparePriceContainer: productContent.querySelector('.quick-shop__price--compareAt'),
      productContentHead: productContent.querySelector('.quick-shop__content-head'),
      swiperNext: quickShopContainer.querySelector('.quick-shop__body--next'),
      swiperPrev: quickShopContainer.querySelector('.quick-shop__body--prev'),
    };
  }

  attachListeners () {
    this.state.swiper.slides.forEach((productCard) => {
      const cartBtn = productCard.querySelector('.product-item__cart-btn');
      cartBtn.addEventListener('click', this.toggleProductView.bind(this, productCard));
    });
    tabletMedia.onchange = this.onResize.bind(this);
    this.state.elements.addToCartBtn.addEventListener('click', this.onAddToCart.bind(this));
  }

  onResize () {
    this.resetCardViews();
    this.state.currentViewProduct = null;
    this.state.contentCalculated = false;
    this.state.elements.quickShopContainer.style.removeProperty('height');
    this.state.variantSelection = {};
    this.toggleProductContent();
  }

  toggleProductView (productCard) {
    if (this.state.currentViewProduct) {
      if (+productCard.dataset.productId !== this.state.currentViewProduct?.id) {
        this.onOpenProductView(productCard);
        return;
      }
      this.onCloseProductView(productCard);
    } else {
      this.onOpenProductView(productCard);
    }
  }

  getProductColorName (product) {
    if (!product || 'error' in product) return
    const colorIndex = product.options?.findIndex((el) => el.toUpperCase() === 'COLOR');
    return product.variants[0].title.split(' / ')[colorIndex];
  }

  setProductColor (product) {
    const colorName = this.getProductColorName(product)?.toUpperCase();
    product.swatchImage = product.images[0];
    product.COLOR = colorName;
  }

  setProductOptions (product, parentProduct) {
    product.variants.forEach((variant) => {
      product.options.forEach((option, optionIndex) => {
        const optionName = option.toUpperCase();
        const optionValue = variant[`option${optionIndex + 1}`];
        variant[optionName] = optionValue;
        const saveOptionsProduct = product || parentProduct;
        if (!('allOptions' in saveOptionsProduct)) {
          saveOptionsProduct.allOptions = {};
        }
        if (!saveOptionsProduct.allOptions[optionName]) {
          saveOptionsProduct.allOptions[optionName] = [];
        }
        if (!saveOptionsProduct.allOptions[optionName].includes(optionValue)) {
          saveOptionsProduct.allOptions[optionName].push(optionValue);
        }
      });
    });
  }

  matchProductOptions () {
    const { products } = window.quickShopSettings[this.id];
    const productIds = products.map(product => product.id);
    this.state.productIds = productIds;
    this.state.allProducts = [];
    productIds.forEach((id) => {
      const product = products.find(product => product.id == id);
      this.setProductColor(product);
      this.setProductOptions(product);
      product.relatedProducts.forEach((relatedProduct) => {
        this.setProductColor(relatedProduct);
        this.setProductOptions(relatedProduct, product);
      });
      this.state.allProducts.push(product, ...product.relatedProducts);
    });
  }

  setProductContentDimensions (activeProductCard) {
    const { quickShopContainer, productContent } = this.state.elements;
    const onTransitionEnd = (event) => {
      if (
        +activeProductCard.dataset.productId !== this.state.currentViewProduct?.id
        || event.propertyName !== 'width'
        || !this.state.currentViewProduct
        || !activeProductCard
      ) {
        return;
      }
      if (event.elapsedTime < 0.3) {
        this.state.contentCalculated = false;
        this.setProductContentDimensions(activeProductCard);
        return;
      }
      const sectionConatinerHeight = quickShopContainer.getBoundingClientRect().height;
      quickShopContainer.style.height = `${sectionConatinerHeight}px`;
      if (this.state.contentCalculated) return;
      const cardBounds = activeProductCard.getBoundingClientRect();
      const productContentWidth = quickShopContainer.clientWidth - (cardBounds.left + cardBounds.width);
      productContent.style.width = !tabletMedia.matches ? `${productContentWidth}px` : '100%';
      this.state.contentCalculated = true;
    };

    activeProductCard.ontransitionend = onTransitionEnd
  }

  toggleProductContent () {
    this.state.elements.productContent.classList.toggle(
      'active',
      !!this.state.currentViewProduct,
    );
  }

  resetCardViews (exceptionCard) {
    this.state.swiper.slides.forEach((slide) => {
      if (slide === exceptionCard) return;
      slide.classList.remove('active');
    });
    this.state.swiper.update();
  }

  onCloseProductView (productCard) {
    if (!this.state.currentViewProduct) {
      productCard = (
        productCard
        || Array.from(this.state.swiper.slides)
          .find((slide) => slide.classList.contains('active'))
      );
      this.onOpenProductView(productCard);
      return;
    }
    this.state.contentCalculated = false;
    this.state.currentViewProduct = null;
    this.state.elements.quickShopContainer.style.removeProperty('height');
    this.toggleProductContent();
    this.state.variantSelection = {};
    this.state.swiper.translateTo(this.state.prevTranslate, 300, false, false);
    this.state.swiper.allowTouchMove = true;
    this.resetCardViews();
  }

  toggleAddToCartBtn (selectedVariant) {
    const { addToCartBtn } = this.state.elements;
    const emptyOption = this.getEmptyOptionName();
    if (emptyOption || !selectedVariant || !selectedVariant?.available) {
      addToCartBtn.setAttribute('disabled', 'disabled');
      addToCartBtn.textContent = (selectedVariant && !selectedVariant.available)
        ? 'Sold out'
        : 'Add to Bag';
      return;
    }
    addToCartBtn.textContent = 'Add to Bag';
    addToCartBtn.removeAttribute('disabled');
  }

  getEmptyOptionName () {
    const { selectedProductColor } = this.state.variantSelection;
    if (!selectedProductColor) {
      return 'COLOR';
    }
    return selectedProductColor.options.find((opt) => {
      const optionName = opt.toUpperCase();
      return (
        optionName !== 'COLOR'
        && !this.state.variantSelection.restOptions?.[optionName]
      );
    })?.toUpperCase();
  }

  setAddToCartLabel (btnEl, label) {
    btnEl.textContent = label;
    setTimeout(() => {
      btnEl.textContent = 'Add to Bag';
    }, 2000);
  }

  async onAddToCart (e) {
    const { currentTarget } = e;
    const selectedVariant = this.getVariantByOptions();
  
    if (!selectedVariant) return;
  
    const cartItems = [{
      id: selectedVariant.id,
      quantity: 1,
      properties: {
        _Color: selectedVariant.COLOR,
        _Fit: selectedVariant.FIT,
        _Size: selectedVariant.SIZE,
        _OriginalPrice: selectedVariant.price,
        _CompareAtPrice: selectedVariant.compare_at_price && selectedVariant.compare_at_price,
        _ProductSku: retrieveSku(selectedVariant.sku),
      },
    }];

    if (
      isFinalSale(selectedVariant.price, selectedVariant.compare_at_price) || 
      isUnreturnable(this.state.variantSelection.selectedProductColor)
    ) {

      cartItems[0].properties._finalSale = 'Final Sale';
      cartItems[0].properties["_LPROP"] = 'Final Sale';
    }

    currentTarget.textContent = 'Adding...';
    try {
      await addItemsToCart(cartItems);
      this.setAddToCartLabel(currentTarget, 'Added to bag');
    } catch (er) {
      console.error(er);
      this.setAddToCartLabel(currentTarget, 'Error');
    }
  }

  setViewProductUrl () {
    this.state.elements.viewProductBtn.setAttribute(
      'href',
      `/products/${this.state.currentViewProduct.handle}`,
    );
  }

  toggleOptionsAvailability () {

  };

  renderContentHead = () => {
    const { currentViewProduct } = this.state;
    const { price, compare_at_price } = currentViewProduct.variants[0];
    const template = `
      <span class="quick-shop__product-title">${currentViewProduct.title}</span>
      <span class="quick-shop__price">$${formatPrice(price / 100)}</span>
      ${compare_at_price
      ? `
          <span class="quick-shop__price quick-shop__price--compareAt">
            $${formatPrice(compare_at_price / 100)} 
          </span>
        `
      : ''
  }
    `;
    this.state.elements.productContentHead.innerHTML = template;
  }

  renderProductColors () {
    const { currentViewProduct } = this.state;
    const productColorsList = [
      currentViewProduct,
      ...currentViewProduct.relatedProducts,
    ];
    const colorItems = productColorsList.map((product, index) => `
        <li
          class="quick-shop__option-item ${index === 0 ? 'active' : ''} quick-shop__option-item--COLOR" 
          tabIndex="0" 
          data-value="${product.COLOR}"
          data-id="${product.id}"
        >
          <img src="${window.resizeImage(product.images[0], 200)}" alt="${product.title}">
        </li>
      `);
    const template = `
      <li class="quick-shop__option" data-option-name="COLOR">
        <ul>${colorItems.join('')}</ul>
      </li>
    `;
    this.state.elements.optionsList.insertAdjacentHTML('afterbegin', template);
    const colorItemEls = Array.from(
      this.state.elements.optionsList.querySelectorAll('.quick-shop__option-item--COLOR'),
    );
    this.state.elements.colorItemEls = colorItemEls;
    colorItemEls.forEach((el) => el.addEventListener('click', (e) => this.onSelectColor(e, undefined, el.dataset.id)))
  }

  getProductById (id) {
    let product = this.state.allProducts.find(product => {
      return product.id === +id
    })
    return product
  }

  toggleVariantsState (selectedOptionName) {
    if (!selectedOptionName) {
      const firstOptionsList = Object.values(this.state.elements.optionEls)[0];
      firstOptionsList?.[0].parentNode.classList[
        this.state.variantSelection.COLOR ? 'remove' : 'add'
      ]('disabled');
      return;
    }
    const optionKeys = Object.keys(this.state.elements.optionEls);
    let optionsList = this.state.elements.optionEls[selectedOptionName];
    const selectedOptionIndex = optionKeys.indexOf(selectedOptionName);
    if (optionKeys.length > 1 && selectedOptionIndex < optionKeys.length - 1) {
      const nextOptionName = optionKeys[selectedOptionIndex + 1];
      optionsList = this.state.elements.optionEls[nextOptionName];
    }
    optionsList[0].parentNode.classList[
      this.state.variantSelection.restOptions[selectedOptionName]
        ? 'remove' : 'add'
    ]('disabled');
  }

  toggleActiveEl (selectedEl, elementsList) {
    elementsList.forEach((item) => {
      item.classList[
        item !== selectedEl ? 'remove' : 'add'
      ]('active');
    });
  }

  setVariantIdUrl (variant) {
    const { selectedProductColor } = this.state.variantSelection;
    if (!variant || !selectedProductColor) return;
    this.state.elements.viewProductBtn.setAttribute(
      'href',
      `/products/${selectedProductColor.handle}?variant=${variant.id}`,
    );
  }

  setProductColorLabel (colorName) {
    this.state.elements.colorLabelContainer.textContent = colorName.toLowerCase();
  }

  onSelectColor (e, colorValue, colorId) {
    let currentTarget;
    let currentValue = colorValue;
    if (!colorValue) {
      currentTarget = e.currentTarget;
      const { dataset: { value } } = currentTarget;
      currentValue = value;
    }
    this.state.variantSelection.COLOR = currentValue;
    this.state.variantSelection.selectedProductColor = this.getProductById(colorId)
    const selectedVariant = this.getVariantByOptions();
    this.setProductColorLabel(currentValue);
    if (!colorValue) {
      this.toggleActiveEl(currentTarget, this.state.elements.colorItemEls);
    }
    this.toggleAddToCartBtn(selectedVariant);
    this.setVariantIdUrl(selectedVariant);
    this.toggleOptionsAvailability();
    this.toggleVariantsState();
  }

  getVariantByOptions (options = this.state.variantSelection.restOptions) {
    const { selectedProductColor } = this.state.variantSelection;
    const selectedOptionKeys = Object.keys(options || {});
    if (selectedProductColor.variants.length === 1) {
      return selectedProductColor.variants[0]
    }
    if (!selectedProductColor || !selectedOptionKeys.length) {
      return null;
    }
    const variant = selectedProductColor.variants.find((variant) => {
      let variantMatches = true;
      selectedOptionKeys.forEach((key) => {
        const selectedOptValue = options[key];
        variantMatches = (
          variantMatches
          && selectedOptValue.toUpperCase() === variant[key]?.toUpperCase()
        );
      });
      return variantMatches;
    });
    return variant;
  }

  onSelectRestOptions (e) {
    const { currentTarget } = e;
    const { dataset: { value, optionName } } = currentTarget;
    if (currentTarget.parentNode.classList.contains('disabled')) {
      return;
    }
    if (!('restOptions' in this.state.variantSelection)) {
      this.state.variantSelection.restOptions = {};
    }
    this.state.variantSelection.restOptions[optionName] = value;
    const selectedVariant = this.getVariantByOptions();
    this.toggleActiveEl(currentTarget, this.state.elements.optionEls[optionName]);
    this.toggleAddToCartBtn(selectedVariant);
    this.setVariantIdUrl(selectedVariant);
    this.toggleOptionsAvailability();
    this.toggleVariantsState(optionName);
  }

  renderProductOptions () {
    const optionNames = Object.keys(this.state.currentViewProduct.allOptions)
      .filter((key) => key !== 'COLOR');
    optionNames.forEach((optionName) => {
      const optionValues = this.state.currentViewProduct.allOptions[optionName];
      const optionItems = optionValues.map((item) => `
          <li 
            class="quick-shop__option-item quick-shop__option-item--${optionName.toUpperCase()}" 
            data-value="${item.toUpperCase()}"
            data-option-name="${optionName.toUpperCase()}"
            tabIndex="0"
          >
            ${item.toLowerCase()}
          </li>
        `);
      const template = `
        <li 
          class="quick-shop__option" 
          data-option-name="${optionName}"
        >
          <ul class="disabled">${optionItems.join(' ')}</ul>
        </li>
      `;
      this.state.elements.optionsList.insertAdjacentHTML('beforeend', template);
      const renderedEls = Array.from(
        this.state.elements.optionsList.querySelectorAll(
          `.quick-shop__option-item--${optionName.toUpperCase()}`,
        ),
      );
      this.state.elements.optionEls[optionName] = renderedEls;
      renderedEls.forEach((el) => {
        el.addEventListener('click', this.onSelectRestOptions.bind(this));
      });
    });
  }

  cleanUpState () {
    this.state.variantSelection = {};
    this.state.elements.optionsList.innerHTML = '';
    this.state.elements.addToCartBtn.setAttribute('disabled', 'disabled');
  }

  isSameProductOpen(productObj) {
    return (
      !productObj
      || (this.state.currentViewProduct && this.state.currentViewProduct === productObj)
    )
  }

  resetCurrentView (productCard) {
    if (this.state.currentViewProduct) {
      this.resetCardViews(productCard);
      this.state.variantSelection = {};
      this.state.elements.colorItemEls = undefined;
      this.state.elements.optionEls = {};
    }
  }

  translateSlider (productCard) {
    const { productId } = productCard.dataset;
    const { swiper } = this.state;
    const productObj = window.quickShopSettings[this.id].products.find(product => product.id == productId);
    let itemIndex = swiper.slides.indexOf(productCard);
    const isBeforeLastSlide = (swiper.slides.length - (1 + LOOP_FALLBACK_BEFORE_SLIDES_COUNT)) === itemIndex;
    const scaledCardWidth = tabletMedia.matches ? '100%' : `${productCard.offsetWidth * PRODUCT_CARD_SCALE}px`;
    let swiperTranslate = -(productCard.offsetWidth * itemIndex - 1);
  
    if (isBeforeLastSlide) {
      const newCardPosition = this.getLoopBackCardPosition();
      swiperTranslate = newCardPosition.swiperTranslate
      productCard = newCardPosition.productCard
      itemIndex = newCardPosition.itemIndex
    }
  
    swiper.translateTo(swiperTranslate - (itemIndex * SLIDE_SPACE_BETWEEN), 300, false, false);
  
    productCard.classList.add('active');
    productCard.style.width = scaledCardWidth;
    this.state.currentViewProduct = productObj;
    this.state.prevTranslate = swiperTranslate;
  
    swiper.allowTouchMove = false;
  }

  getLoopBackCardPosition () {
    const { swiper } = this.state;
    const adjacentSlide = swiper.slides[swiper.slides.length - (1 + LOOP_FALLBACK_BEFORE_SLIDES_COUNT)];
    const loopBackIndex = parseInt(adjacentSlide.dataset.swiperSlideIndex);
    swiper.slideToLoop(loopBackIndex, 0);
    const productCard = this.state.elements.quickShopSwiperContainer.querySelector(`[data-swiper-slide-index="${loopBackIndex}"]`);
    const itemIndex = swiper.slides.indexOf(productCard);
    const swiperTranslate = -(productCard.offsetWidth * itemIndex);

    return {productCard, itemIndex, swiperTranslate}
  }

  onOpenProductView (productCard) {
    const { productId } = productCard.dataset;
    const productObj = window.quickShopSettings[this.id].products.find(product => product.id == productId);
    const { swiper } = this.state;
    if (this.isSameProductOpen(productObj)) return
    if (productObj.variants.length === 1) {
      this.state.variantSelection.selectedVariant = productObj.variants[0]
    }
    this.resetCurrentView(productCard) 
    this.translateSlider(productCard)
    this.setProductContentDimensions(productCard);
    this.cleanUpState();
    this.renderContentHead();
    this.renderProductColors();
    this.setViewProductUrl();
    this.renderProductOptions();
    this.toggleProductContent();
    this.setViewProductUrl();
    this.onSelectColor(
      undefined, 
      this.state.currentViewProduct.COLOR, 
      this.state.currentViewProduct.id
    );
  }

  stopScroll = function(direction) {
    clearInterval(this.state.scrollInterval[direction])
  }

  autoPlaySliderOnHover (direction) {
    const scroll = () => {
      if (this.state.scrollInterval.isMoving) return
      this.state.scrollInterval.isMoving = true
      if (this.state.currentViewProduct) return
      if (direction === "slidePrev") {
        this.state.swiper.slideToLoop(this.state.swiper.realIndex - 1, 1500)
      } else if (direction === "slideNext") {
        this.state.swiper.slideToLoop(this.state.swiper.realIndex + 1, 1500)
      }
    }
  
    scroll()
    this.stopScroll(direction)
    this.state.scrollInterval[direction] = setInterval(scroll, 0)
  }

  onAfterSwiperInit() {
    const {swiperPrev, swiperNext} = this.state.elements

    swiperPrev.addEventListener(
      'mouseover', this.autoPlaySliderOnHover.bind(this,"slidePrev")
    )
    swiperNext.addEventListener(
      'mouseover', this.autoPlaySliderOnHover.bind(this, "slideNext")
    )
    swiperPrev.addEventListener(
      'mouseout', this.stopScroll.bind(this, "slidePrev")
    )
    swiperNext.addEventListener(
      'mouseout', this.stopScroll.bind(this, "slideNext")
    )
  }

  initSwiper () {
    this.state.swiper = new Swiper(`#quick-shop-${ this.id } .quick-shop__body`, {
      slidesPerView: 2.2,
      freeMode: false,
      spaceBetween: SLIDE_SPACE_BETWEEN,
      loop: true,
      updateOnWindowResize: false,
      loopAdditionalSlides: this.state.productIds.length * 4,
      centeredSlides: true,
      resistance: false,
      breakpoints: {
        1100: { slidesPerView: 4.2 },
      },
      on: {
        afterInit: this.onAfterSwiperInit.bind(this),
        slideChangeTransitionEnd: () => {this.state.scrollInterval.isMoving = false}
      }
    });
    this.state.swiper.loopFix();
    this.state.prevTranslate = this.state.swiper.getTranslate();
    setTimeout(() => {
      this.state.elements.quickShopSwiperContainer.classList.add('enable-transition');
    }, 1000);
  }
}

const init = () => {
  Object.entries(window.quickShopSettings).forEach(([k, v]) => {
    if (!v.simplified) {
      new QuickShop(k)
    }
  })
}

onDocumentReady(init);